<template>
  <div class="hello">
    <rs-panes split-to="columns" :allow-resize="false" :size="250">
      <div class="hello menu" slot="firstPane">
        <div class="title">
          <h2>Vue-resize-split-panel</h2> 
          <i>in level1.firstPane</i>
        </div>
        <div>
          <label for="rSize">Dynamic size: {{localSize}}px</label>
          <button id="rSize" @click="setSize">New size</button>
        </div>
        <div>
          <label for="ResizerWidth">Resizer thickness: {{localResizerThickness}}px</label>
          <input id="ResizerWidth" type="range" min="1" max="15" step="1" v-model="localResizerThickness"/> 
        </div>
        <div>
          <label for="ResizerColor">Resizer color: {{localResizerColor}}</label>
          <input id="ResizerColor" type="color" v-model="localResizerColor"/> <br/>
          <small>This is html color picker so transparency is not available.</small>
        </div>
        <div>
          <label for="BorderWidth">Resizer border width: {{localBorderThickness}}px</label>
          <input id="BorderWidth" type="range" min="0" max="10" step="1" v-model="localBorderThickness"/> <br/>
          <small>Mouse over resizer to see the border</small>
        </div>
        <div>
          <label for="BorderColor">Resizer color: {{localResizerBorderColor}}</label>
          <input id="BorderColor" type="color" v-model="localResizerBorderColor"/> <br/>
          <small>This is html color picker so transparency is not available.</small>
        </div>

      </div>
      <div slot="secondPane">
        <rs-panes
        split-to="columns"
        :allow-resize="true"
        :size="+localSize"
        v-on:update:size="localSize = $event"
        :resizerColor="localResizerColor"
        :resizerBorderColor="localResizerBorderColor"
        :resizerThickness="+localResizerThickness"
        :resizerBorderThickness="+localBorderThickness">
          <div slot="firstPane">
            <rs-panes
            split-to="rows"
            :allow-resize="true"
            :size="+450"
            :resizerColor="localResizerColor"
            :resizerBorderColor="localResizerBorderColor"
            :resizerThickness="+localResizerThickness"
            :resizerBorderThickness="+localBorderThickness">
              <div slot="firstPane">
                <div class="paneContent">
                  
                  <h3> Panes can be nested as deep as you want!</h3>
                  <p> This entire page is made out of <b>ResizeSplitPane</b>.</p>
                  <p> Menu is in <b>level1.firsPane</b> and resizing is disabled!</p>
                  <h4>level1.secondPane > level2.firstPane > level3.firstPane</h4>
                </div>
              </div>
              <div slot="secondPane">
                <div class="paneContent">
                  
                  <h3> Each ResizeSplitPane has his own settings.</h3>
                  <p> Or they can share the same data!</p>
                  <h4>level1.secondPane > level2.firstPane > level3.secondPane</h4>
                </div>
              </div>
      </rs-panes>
          </div>
          <div slot="secondPane">
            <div class="paneContent">
              <div class="paneContent">
                  
                  <h3>Links:</h3>
                  <p> <a href="https://github.com/raven78/vue-resize-split-pane" target="_blank">GitHub repo</a></p>
                  <p><a href="https://www.npmjs.com/package/vue-resize-split-pane" target="_blank">Node module</a></p>
                  <h4>level1.secondPane > level2.secondPane</h4>
                </div>
            </div>
          </div>
      </rs-panes>
      </div>
  
    </rs-panes>
  </div>
</template>

<script>
import ResSplitPane from './ResizeSplitPane'
export default {
  name: 'HelloWorld',
  components: {
    'rs-panes': ResSplitPane,
  },
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
      localSize: 250,
      localResizerColor: '#DEF',
      localResizerBorderColor: 'rgba(0,0,0,.2)',
      localResizerThickness: 2,
      localBorderThickness: 2,
    }
  },
  methods: {
    setSize() {
      this.localSize = Math.ceil(Math.random() * 500)
    },
    setResizerWidth() {
      this.localResizerThickness = Math.random() * 20
    },
    setResizerBorderWidth() {
      this.localBorderThickness = Math.random() * 20
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  background-color: chocolate;
  display: inline-block;
  height: 100%;
  box-sizing: border-box;
  font-size: 12px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.menu {
  padding: 5px;
  color: rgb(65, 24, 5);
  background-clip: border-box;
}
.menu label {
  font-weight: 600;
  margin-bottom: 10px;
  display: block;
  border-top: 1px solid;
  padding-top: 5px;
}
h2 {
  margin-bottom: 0;
}
.menu .title {
  color: maroon;
  text-align: center;
  margin-bottom: 50px;
}

.menu div {
  margin: 15px 0;
}
.paneContent {
  padding: 5px;
}

.paneContent h3 {
  color: rgb(65, 24, 5);
  font-size: 14px;
  margin-bottom: 0;
}
.paneContent p {
  margin-top: 3px;
  color: rgb(129, 59, 27);
}
.paneContent h4 {
  color: lightsteelblue;
}
</style>



